*, *::before, *::after{
  box-sizing:border-box
}

body{
  margin:0px;
  --tw-bg-opacity:1;
  background-color:rgba(249, 250, 251, var(--tw-bg-opacity))
}

.header{
  display:flex;
  align-items:center;
  justify-content:space-between;
  --tw-bg-opacity:1;
  background-color:rgba(255, 255, 255, var(--tw-bg-opacity));
  padding:0.75rem;
  --tw-shadow:0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  box-shadow:var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow)
}

.header .title-menu{
  flex-grow:1;
  overflow:hidden;
  white-space:nowrap;
  padding-left:0.5rem;
  padding-right:0.5rem
}

.header .title-menu .menu-information{
  display:flex;
  align-items:center
}

.header .title-menu .menu-information > :not([hidden]) ~ :not([hidden]){
  --tw-space-x-reverse:0;
  margin-right:calc(1rem * var(--tw-space-x-reverse));
  margin-left:calc(1rem * calc(1 - var(--tw-space-x-reverse)))
}

.header .title-menu .menu-information{
  font-size:0.875rem;
  line-height:1.25rem
}

.header .title-menu .menu-information .menu{
  margin-left:0.25rem;
  display:flex;
  align-items:center;
  font-size:0.875rem;
  line-height:1.25rem;
  --tw-text-opacity:1;
  color:rgba(75, 85, 99, var(--tw-text-opacity))
}

.header .title-menu .menu-information .menu .item{
  position:static;
  cursor:pointer;
  -webkit-user-select:none;
      -ms-user-select:none;
          user-select:none;
  transition-timing-function:cubic-bezier(0, 0, 0.2, 1)
}

.header .title-menu .menu-information .menu .item span{
  display:block;
  height:100%;
  width:100%;
  -webkit-user-select:none;
      -ms-user-select:none;
          user-select:none;
  border-radius:0.5rem
}

.header .title-menu .menu-information .menu .item span:hover{
  --tw-bg-opacity:1;
  background-color:rgba(219, 234, 254, var(--tw-bg-opacity))
}

.header .title-menu .menu-information .menu .item span{
  padding-left:0.5rem;
  padding-right:0.5rem;
  padding-top:0.25rem;
  padding-bottom:0.25rem;
  transition-property:background-color, border-color, color, fill, stroke, opacity, box-shadow, -webkit-transform, -webkit-filter, -webkit-backdrop-filter;
  transition-property:background-color, border-color, color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
  transition-property:background-color, border-color, color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter, -webkit-transform, -webkit-filter, -webkit-backdrop-filter;
  transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration:150ms;
  transition-duration:200ms
}

.header .title-menu .menu-information .menu .item.disable span:hover{
  --tw-bg-opacity:1;
  background-color:rgba(243, 244, 246, var(--tw-bg-opacity))
}

.header .title-menu .menu-information .menu .item.disable span{
  --tw-text-opacity:1;
  color:rgba(209, 213, 219, var(--tw-text-opacity))
}

.header .title-menu .menu-information .menu .item .submenu{
  position:absolute;
  z-index:20;
  margin-top:0.5rem;
  margin-bottom:0.5rem
}

.header .title-menu .menu-information .menu .item .submenu > :not([hidden]) ~ :not([hidden]){
  --tw-space-y-reverse:0;
  margin-top:calc(0.25rem * calc(1 - var(--tw-space-y-reverse)));
  margin-bottom:calc(0.25rem * var(--tw-space-y-reverse));
  --tw-divide-y-reverse:0;
  border-top-width:calc(1px * calc(1 - var(--tw-divide-y-reverse)));
  border-bottom-width:calc(1px * var(--tw-divide-y-reverse));
  border-style:solid;
  --tw-divide-opacity:1;
  border-color:rgba(229, 231, 235, var(--tw-divide-opacity))
}

.header .title-menu .menu-information .menu .item .submenu{
  border-radius:0px;
  border-width:1px;
  border-style:solid;
  --tw-border-opacity:1;
  border-color:rgba(229, 231, 235, var(--tw-border-opacity));
  --tw-bg-opacity:1;
  background-color:rgba(255, 255, 255, var(--tw-bg-opacity));
  min-width:5%
}

.header .title-menu .menu-information .menu .item .submenu .sub-item{
  width:100%;
  -webkit-user-select:none;
      -ms-user-select:none;
          user-select:none
}

.header .title-menu .menu-information .menu .item .submenu .sub-item:hover{
  --tw-bg-opacity:1;
  background-color:rgba(219, 234, 254, var(--tw-bg-opacity))
}

.header .title-menu .menu-information .menu .item .submenu .sub-item{
  padding:0.5rem;
  padding-left:1rem;
  padding-right:1rem
}

.header .title-menu .menu-information .info{
  --tw-text-opacity:1;
  color:rgba(107, 114, 128, var(--tw-text-opacity));
  text-decoration:underline
}

.header .user{
  display:flex;
  align-items:center
}

.header .hide-span{
  position:absolute;
  white-space:pre;
  opacity:0;
  z-index:-100
}

.wrapper{
  position:static
}

.wrapper .cursor{
  position:absolute;
  z-index:5
}

.wrapper .editor .ql-editor{
  --tw-bg-opacity:1;
  background-color:rgba(255, 255, 255, var(--tw-bg-opacity));
  --tw-shadow:0 25px 50px -12px rgba(0, 0, 0, 0.25);
  box-shadow:var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  width:8.5in;
  min-height:11in;
  padding:1in;
  margin:1rem
}

.wrapper .editor .ql-container.ql-snow{
  display:flex;
  justify-content:center;
  border-style:none
}

.wrapper .editor .ql-toolbar.ql-snow{
  position:sticky;
  top:0px;
  z-index:10;
  display:flex;
  border-style:none;
  --tw-bg-opacity:1;
  background-color:rgba(229, 231, 235, var(--tw-bg-opacity));
  padding-left:2rem;
  --tw-shadow:0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  box-shadow:var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow)
}

@page{
  margin:1in
}

@media print{
  body{
    background:none
  }

  .header{
    display:none
  }

  .wrapper .editor .ql-editor{
    width:6.5in;
    height:9in;
    padding:0;
    margin:0;
    box-shadow:none;
    align-self:flex-start
  }

  .wrapper .editor .ql-toolbar.ql-snow{
    display:none
  }
}

